.container {
    margin-left: auto;
    margin-right: auto;
    max-width: 98%;
}

[class*="column"] {
    padding: 5px;
}

img {
    margin-left: auto;
    margin-right: auto;
}

hr {
    border: 0.8px solid red;
    width: 100%;
}

.center {
    margin-left: auto;
    margin: auto;
    text-align: center;
}

.heading {
    font-weight: bold;
}

.justify {
    text-align: justify;
}

.row-8 span {
    color: red;
}

.row-9 {
    margin-top: 15px !important;
}

.row-9 [class*="column"] {
    background: red;
    color: white;
}

.row-9 [class*="column"] a {
    color: white;
    text-decoration: none;
}

@media screen and (min-width: 768px) {
    [class*="row"] {
        display: -webkit-flex;
        display: -ms-flex;
        display: -moz-flex;
        display: flex;
        margin-top: 10px;
    }
    .row-1 {
        -webkit-flex: 1;
        -ms-flex: 1;
        -moz-flex: 1;
        flex: 1;
    }
    .row-1 img {
        max-width: 30%;
    }
    .row-2 .column-1 {
        -webkit-flex: 1;
        -ms-flex: 1;
        -moz-flex: 1;
        flex: 1;
    }
    .row-2 .column-1 img {
        max-width: 100%;
    }
    .row-2 .column-2 {
        -webkit-flex: 2;
        -ms-flex: 2;
        -moz-flex: 2;
        flex: 2;
    }
    .row-2 .column-2 img {
        max-width: 100%;
    }
    .row-4 img {
        max-width: 80%;
    }
    .row-9 [class*="column"] {
        -webkit-flex: 1;
        -ms-flex: 1;
        -moz-flex: 1;
        flex: 1;
    }
}

@media screen and (max-width: 767px) {
    img {
        max-width: 100%;
    }
    .row-9 [class*="column"] {
        border: 1px solid white;
    }
}